import './App.scss';
import Entete from './Entete';
import PiedPage from './PiedPage';
import ListeProduits from './ListeProduits';
import {useState} from 'react';

function App() {
  // tat React pour grer un panier d'achats
  const etatPanier = useState({});
  // Remarquez que useState retourne un tableau : 
  // Le premier lment du tableau reprsente le contenu de l'tat
  const panier = etatPanier[0]; 
  // Le deuxime lment est une fonction qui sert  rcrire l'tat
  // const setPanier = etatPanier[1]; 
  // Donc, alternativement avec destructuration de tableau
  // const [panier, setPanier] = useState({});

  // tat React pour grer un compteur de clics
  // Remarquez la syntaxe JS de "dstructuration" de tableau : on obtient 
  // rapidement deux variables contenant les deux lments du tableau retourn 
  // par useState()
  const [compteur, setCompteur] = useState(0); 

  return (
    <div className="App">
      <Entete panier={panier} test="Allo Props"/>
      <ListeProduits etatPanier={etatPanier} />
      <div>
        <span>Nombre de clics : {compteur} </span>
        <button onClick={() => {setCompteur(compteur+1); console.log('Compteur des clics : ', compteur);}}>Cliquez-moi</button>
      </div>
      <PiedPage />
    </div>
  );
}

export default App;
